/*
   * Стили компонента Modal
   */

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 15vw;
  padding-right: 15vw;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}
